:root {
    font-size: 62.5%;
}

html {
    &.is-fixed {
        overflow: hidden;
    }
}

body {
    font-family: Arial, "微軟正黑體", "Microsoft JhengHei", "Arial Unicode MS", "Helvetica Neue", Helvetica, sans-serif !important;
    &.is-fixed {
        overflow: hidden;
    }
    background-color: #fff;
}

.wrap {
    max-width: 1920px;
    margin: 0 auto;
    overflow: hidden;
}

.rsp-img {
    max-width: 100%;
    display: block;
    margin: 0 auto;
    height: auto;
}

.rsp-h-img {
    width: auto;
    display: block;
    margin: 0 auto;
    height: 100%;
}

.site-main {
    &__inner {
        box-sizing: border-box;
        max-width: 76.8rem;
        margin: 0 auto;
        border-top: solid 0.1rem #c1c1c4;
        border-bottom: solid 0.1rem #c1c1c4;
        position: relative;
        padding: 0.6rem;
        &::before,
        &::after {
            width: 0.1rem;
            height: 100%;
            position: absolute;
            content: "";
            top: 0;
            background: rgb(193,193,196);
            background: linear-gradient(0deg, rgba(193,193,196,1) 0%, rgba(193,193,196,0) 35%, rgba(193,193,196,0) 70%, rgba(193,193,196,1) 100%);
        }
        &::before {
            left: 0;
        }
        &::after {
            right: 0;
        }
    }
    &__content {
        background-color: #333438;
        min-height: 306rem;
    }
}

.inner {
    width: 76.8rem;
    margin: 0 auto;
}