.popup {
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 550;
    pointer-events: none;
    opacity: 0;
    transition: all 500ms ease;
    &.is-active {
        pointer-events: auto;
        opacity: 1;
        z-index: 550;
    }
    &__overlay {
        position: absolute;
        width: 100%;
        height: 100%;
        background: transparent url('../images/popup-bg.png') repeat 0 0;
        display: flex;
        justify-content: center;
        align-items: center;
        @include susy-breakpoint($s-320, $c8) {
            align-items: flex-start;
        }
    }
    &__inner {
        @include susy-breakpoint($s-320, $c8) {
            padding-top: 8rem;
        }
    }
    &__btn-close {
        width: 8.7rem;
        height: 9rem;
        background: transparent url('../images/btn-close@2x.png') repeat 0 0;
        background-size: cover;
        border: 0;
        padding: 0;
        margin: 0 auto 2rem;
        display: block;
    }
    &__title,
    &__footer {
        img {
            max-width: 100%;
            display: block;
            margin: 0;
        }
    }
    &__event-1 {
        width: 78.2rem;
        box-sizing: border-box;
        @include susy-breakpoint($s-320, $c8) {
            height: 99.5rem;
        }
    }
    &__event-2 {
        width: 78.2rem;
        box-sizing: border-box;
        @include susy-breakpoint($s-320, $c8) {
            height: 99.5rem;
        }
    }
    &__event-3 {
        width: 78.2rem;
        box-sizing: border-box;
        @include susy-breakpoint($s-320, $c8) {
            height: 99.5rem;
        }
    }
    &__content {
        box-sizing: border-box;
        height: 50rem;
        padding: 0 4.5rem;
        background: transparent url('../images/event-popup-body@2x.png') repeat-y center top;
        background-size: 78.2rem auto;
        @include susy-breakpoint($s-320, $c8) {
            height: calc(100vh - 58rem);
        }
        &.event-2 {
            height: 28rem;
        }
    }
    &__btn-knew {
        width: 27.1rem;
        height: 10.9rem;
        background: transparent url('../images/btn-knew@2x.png') repeat 0 0;
        background-size: cover;
        display: block;
        margin: 0 auto;
        border: 0;
        font-size: 0;
    }
}

.event-1 {
    &__text {
        font-size: 3rem;
        color: #000;
        line-height: 1.2;
        margin-bottom: 2rem;
    }
    &__remind-text {
        padding-top: 3rem;
        font-size: 3rem;
        color: #000;
        line-height: 1.2;
        margin-bottom: 2rem;
    }
    &__step-1 {
        margin-bottom: 1rem;
        h3 {
            color: #a85402;
            font-size: 3.5rem;
            margin-bottom: 2rem;
        }
    }
    &__ol-list {
        counter-reset: ol-list;
        & > li {
            counter-increment: ol-list;
            font-size: 3rem;
            color: #000;
            line-height: 1.2;
            position: relative;
            padding-left: 3rem;
            &:not(:last-child) {
                margin-bottom: 4rem;
            }
            &::before {
                content: counter(ol-list) ".";
                font-size: 3rem;
                position: absolute;
                color: #000;
                left: 0;
                top: 0;
            }
        }
    }
    &__inner-ul-list {
        padding-top: 1rem;
        li {
            font-size: 3rem;
            color: #000;
            line-height: 1.2;
            padding-left: 3rem;
            position: relative;
            &::before {
                content: "◆";
                font-size: 3rem;
                color: #000;
                position: absolute;
                left: 0;
                top: 0;
                line-height: 1.2;
            }
        }
    }
    &__prize {
        padding-top: 2rem;
        padding-left: 1rem;
        padding-right: 1rem;
        margin-bottom: 1rem;
        h4 {
            color: #0063c2;
        }
    }
    &__prize-head {
        background: rgb(1,62,119);
        background: radial-gradient(circle, rgba(1,62,119,1) 0%, rgba(1,31,60,1) 100%);
        display: flex;
        padding: 0.6rem 0;
        position: relative;
        &::before {
            content: "";
            position: absolute;
            left: 0;
            bottom: -0.6rem;
            width: 0;
            height: 0;
            border-left: 0.7rem solid transparent;
            border-right: 0.7rem solid transparent;
            border-bottom: 0.7rem solid #002950;
            transform: rotate(45deg);
        }
        &::after {
            content: "";
            position: absolute;
            right: 0;
            bottom: -0.6rem;
            width: 0;
            height: 0;
            border-left: 0.7rem solid transparent;
            border-right: 0.7rem solid transparent;
            border-bottom: 0.7rem solid #002950;
            transform: rotate(-45deg);
        }
        span {
            color: #ffdd8b;
            font-size: 2.8rem;
            text-align: center;
            box-sizing: border-box;
            &:nth-child(1) {
                flex: 0 0 42.5%;
                border-right: solid 0.1rem #fff;
                padding-left: 2rem;
            }
            &:nth-child(2) {
                flex: 0 0 57.5%;
                padding-right: 1.8rem;
            }
        }
    }
    &__prize-wrap {
        background-color: #000;
        padding: 1rem;
        margin: 0 1rem;
        border: solid 0.1rem #fff;
        border-top: 0;
    }
    &__prize-row {
        display: flex;
        align-items: stretch;
        &:not(:last-child) {
            border-bottom: solid 0.1rem #808080;
        }
        span {
            box-sizing: border-box;
            padding: 1rem 0;
            text-align: center;
            display: flex;
            align-items: center;
            justify-content: center;
            color: #fff;
            font-size: 2.6rem;
            &:nth-child(1) {
                flex: 0 0 42%;
                border-right: solid 0.1rem #fff;
            }
            &:nth-child(2) {
                flex: 0 0 58%;
            }
        }
    }
}

.event-2 {
    &__text {
        font-size: 3rem;
        color: #000;
        line-height: 1.2;
        margin-bottom: 2rem;
        a {
            color: #0063c2;
            border-bottom: 0.1rem solid #0063c2;
        }
    }
    &__step-1 {
        margin-bottom: 1rem;
        h3 {
            color: #a85402;
            font-size: 3.5rem;
            margin-bottom: 0.2rem;
        }
    }
    &__prize {
        padding-top: 2rem;
        padding-left: 1rem;
        padding-right: 1rem;
        h4 {
            color: #0063c2;
        }
    }
    &__prize-head {
        background: rgb(1,62,119);
        background: radial-gradient(circle, rgba(1,62,119,1) 0%, rgba(1,31,60,1) 100%);
        display: flex;
        padding: 0.6rem 0;
        position: relative;
        &::before {
            content: "";
            position: absolute;
            left: 0;
            bottom: -0.6rem;
            width: 0;
            height: 0;
            border-left: 0.7rem solid transparent;
            border-right: 0.7rem solid transparent;
            border-bottom: 0.7rem solid #002950;
            transform: rotate(45deg);
        }
        &::after {
            content: "";
            position: absolute;
            right: 0;
            bottom: -0.6rem;
            width: 0;
            height: 0;
            border-left: 0.7rem solid transparent;
            border-right: 0.7rem solid transparent;
            border-bottom: 0.7rem solid #002950;
            transform: rotate(-45deg);
        }
        span {
            color: #ffdd8b;
            font-size: 2.8rem;
            text-align: center;
            box-sizing: border-box;
            &:nth-child(1) {
                flex: 0 0 20rem;
                border-right: solid 0.1rem #fff;
                padding-left: 2.6rem;
            }
            &:nth-child(2) {
                flex: 0 0 27rem;
                border-right: solid 0.1rem #fff;
            }
            &:nth-child(3) {
                flex: 1 0 auto;
                padding-right: 1.6rem;
            }
        }
    }
    &__prize-wrap {
        background-color: #000;
        padding: 1rem;
        margin: 0 1rem 3rem;
        border: solid 0.1rem #fff;
        border-top: 0;
    }
    &__prize-row {
        display: flex;
        align-items: stretch;
        &:not(:last-child) {
            border-bottom: solid 0.1rem #808080;
        }
        span {
            box-sizing: border-box;
            padding: 1rem 0;
            text-align: center;
            color: #fff;
            font-size: 2.6rem;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-wrap: wrap;
            &:nth-child(1) {
                flex: 0 0 30%;
                border-right: solid 0.1rem #fff;
            }
            &:nth-child(2) {
                flex: 0 0 45%;
                border-right: solid 0.1rem #fff;
                strong {
                    color: #ffba00;
                }
            }
            &:nth-child(3) {
                flex: 0 0 25%;
            }
        }
    }
}