.site-header {
  position: fixed;
  width: 100%;
  height: 10rem;
  background-color: rgba(0,0,0,0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  padding: 0 2rem;
  z-index: 100;
  @include susy-breakpoint($s-320, $c8) {
    background-color: transparent;
    justify-content: flex-end;
    position: absolute;
  }
  &__logo {
    position: absolute;
    width: 9.6rem;
    height: 8.9rem;
    left: 1rem;
    @include susy-breakpoint($s-320, $c8) {
      margin-right: 0;
    }
  }
}

.site-nav {
  display: flex;
  @include susy-breakpoint($s-320, $c8) {
    display: none;
  }
  &__btn {
    font-size: 3rem;
    color: #a7a7a7;
    font-weight: 300;
    display: block;
    margin-right: 2.8rem;
    padding-right: 2.8rem;
    position: relative;
    line-height: 3rem;
    transition: color 300ms ease-in-out;
    &::after {
      width: 0.1rem;
      height: 3rem;
      background-color: #5b5851;
      position: absolute;
      right: 0;
      top: 50%;
      transform: translate3d(0,-50%,0);
      content: "";
    }
    &:last-child {
      padding-right: 0;
      margin-right: 0;
      &::after {
        display: none;
      }
    }
    &:hover {
      color: #ffffff;
    }
  }
}