.info {
    background: #000 url('../images/info-bg.jpg') no-repeat center top;
    height: 91rem;
    box-sizing: border-box;
    padding-top: 8rem;
    &__row {
        margin-bottom: 5rem;
        display: flex;
        align-items: center;
        flex-wrap: wrap;
    }
    &__title {
        flex: 1;
        padding-left: 4rem;
        img {
            width: auto;
            height: 8.4rem;
        }
    }
    &__content {
        box-sizing: border-box;
        flex: 0 0 38.4rem;
        padding-left: 1.2rem;
        &.full {
            flex: 0 0 100%;
            padding-top: 3rem;
        }
        p {
            font-size: 3rem;
            color: #000;
            line-height: 1.1;
            margin-bottom: 0.2rem;
        }
    }
    &__serve {
        width: 32rem;
        display: flex;
        justify-content: space-between;
        font-size: 3rem;
        color: #000;
        line-height: 1.1;
    }
    &__detail {
        display: flex;
        position: relative;
        padding-left: 1rem;
        align-items: center;
        &:not(:last-child) {
            margin-bottom: 4rem;
            &::after {
                content: "";
                width: calc(100% - 4rem);
                position: absolute;
                height: 0.1rem;
                background-color: #b0b0b1;
                bottom: -1.7rem;
                left: 50%;
                transform: translate3d(-50%,0,0);
            }
        }
    }
    &__label {
        word-break: break-all;
        width: 4.1rem;
        height: 12.1rem;
        background: transparent url('../images/info-method-bg@2x.png') no-repeat 0 0;
        background-size: cover;
        font-size: 3rem;
        color: #ffecd1;
        text-align: center;
        display: flex;
        align-items: center;
        margin-right: 2rem;
    }
    &__btn {
        position: absolute;
        right: 2.4rem;
        top: 0.5rem;
        width: 27.1rem;
        height: 10.9rem;
        font-size: 0;
        padding: 0;
        border: 0;
        &.team {
            background: transparent url('../images/info-btn-team@2x.png') no-repeat 0 0;
            background-size: cover;
        }
        &.join{
            background: transparent url('../images/info-btn-join@2x.png') no-repeat 0 0;
            background-size: cover;
        }
    }
    &__ol-list {
        counter-reset: ol-list;
        li {
            counter-increment: ol-list;
            font-size: 3rem;
            position: relative;
            padding-left: 3rem;
            color: #000;
            line-height: 1.1;
            &:not(:last-child) {
                margin-bottom: 1rem;
            }
            &::before {
                content: counter(ol-list) ".";
                font-size: 3rem;
                position: absolute;
                color: #000;
                left: 0;
                top: 0;
            }
        }
    }
    &__remind-text {
        text-align: center;
        padding-top: 1rem;
    }
    &__btn-detail {
        display: inline-block;
        position: relative;
        font-size: 2.5rem;
        color: #4586e9;
        margin-left: 7rem;
        margin-top: 1rem;
        &::before {
            width: 2.8rem;
            height: 2.9rem;
            background: transparent url('../images/info-detail-icon@2x.png') no-repeat 0 0;
            background-size: 2.8rem 2.9rem;
            position: absolute;
            content: "";
            left: -3rem;
            top: 0;
        }
        &::after {
            content: "";
            position: absolute;
            left: 0;
            bottom: -0.2rem;
            width: 100%;
            height: 0.1rem;
            background-color: #4586e9;
        }
    }
}