.rule {
    background: #000 url('../images/rule-bg.jpg') no-repeat center top;
    height: 110.7rem;
    position: relative;
    box-sizing: border-box;
    padding-top: 7.2rem;
    &__title {
        text-align: center;
        img {
            width: auto;
            height: 10.1rem;
            display: inline-block;
        }
    }
    &__item {
        margin-bottom: 2rem;
    }
    &__label {
        position: relative;
        text-align: left;
        display: inline-block;
        margin-bottom: 1.1rem;
        span {
            font-size: 3rem;
            color: #ffe3be;
            box-sizing: border-box;
            display: inline-block;
            position: relative;
            z-index: 20;
            height: 3.8rem;
            line-height: 3.8rem;
            border: solid 0.1rem #ffe3be;
            padding: 0 1.6rem;
        }
        &::after {
            content: "";
            width: calc(100% + 0.8rem);
            height: calc(100% + 0.8rem);
            background-color: #013e77;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate3d(-50%,-50%,0);
            z-index: 10;
        }
    }
    &__content {
        padding-top: 1.6rem;
        padding-left: 2rem;
        padding-right: 2rem;
        p {
            font-size: 3rem;
            color: #000;
            line-height: 1.2;
            margin-bottom: 2.6rem;
        }
    }
}