.remind {
    background: #000 url('../images/remind-bg.jpg') no-repeat center top;
    height: 108rem;
    position: relative;
    box-sizing: border-box;
    padding-top: 7.2rem;
    &__title {
        text-align: center;
        img {
            width: auto;
            height: 10.1rem;
            display: inline-block;
        }
    }
    &__content {
        padding: 0 2rem;
    }
    &__ol-list {
        padding-top: 1.6rem;
        counter-reset: remind-ol-list;
        li {
            counter-increment: ol-list;
            font-size: 3rem;
            color: #fff;
            line-height: 1.2;
            position: relative;
            padding-left: 3rem;
            &:not(:last-child) {
                margin-bottom: 1rem;
            }
            &::before {
                content: counter(ol-list) ".";
                font-size: 3rem;
                position: absolute;
                color: #fff;
                left: 0;
                top: 0;
            }
        }
    }
}