.left-nav {
  z-index: 100;
  width: 42.5rem;
  height: calc(100vh - 10rem);
  position: fixed;
  left: 0;
  top: 10rem;
  background-color: rgba(0,0,0,0.8);
  padding-top: 4.5rem;
  box-sizing: border-box;
  transition: all 800ms ease;
  transform: translate3d(-42.5rem,0,0);
  &.is-active {
    transform: translate3d(0,0,0);
  }
  @include susy-breakpoint($s-320, $c8) {
    top: 0;
    z-index: 120;
    width: 69rem;
    transform: translate3d(-69rem,0,0);
    padding-top: 5rem;
    height: 100vh;
  }
  &__list {
    height: 100%;
    @include susy-breakpoint($s-320, $c8) {
      overflow-y: auto;
    }
  }
  &__btn-toggle {
    width: 7.1rem;
    height: 12.1rem;
    position: absolute;
    right: -7.1rem;
    bottom: 12rem;
    background: transparent url('../images/remote-arrow@2x.png') no-repeat 0 0;
    background-size: cover;
    cursor: pointer;
    @include susy-breakpoint($s-320, $c8) {

    }
  }
  &__btn {
    width: 34rem;
    font-size: 2rem;
    color: #a7a7a7;
    display: block;
    margin-left: 6rem;
    margin-bottom: 3.8rem;
    padding-bottom: 3.8rem;
    position: relative;
    transition: color 300ms ease-in-out;
    @include susy-breakpoint($s-320, $c8) {
      font-size: 3.4rem;
      width: 60rem;
      margin-bottom: 5rem;
      padding-bottom: 5rem;
    }
    &.mobile {
      display: none;
      @include susy-breakpoint($s-320, $c8) {
        display: block;
      }
    }
    &::after {
      position: absolute;
      bottom: 0;
      left: 0;
      content: "";
      width: 100%;
      height: 0.2rem;
      background: rgb(83,81,76);
      background: linear-gradient(90deg, rgba(83,81,76,1) 0%, rgba(41,39,32,1) 100%);
    }
    &:hover {
      color: #f8b876;
    }
  }
}