.privacy {
    box-sizing: border-box;
    position: fixed;
    width: 100%;
    background-color: rgba(0,0,0,0.7);
    height: 23.5rem;
    padding: 5rem 0;
    z-index: 300;
    left: 0;
    bottom: 0;
    opacity: 0;
    pointer-events: none;
    transition: all 500ms ease;
    &.is-active {
        opacity: 1;
        pointer-events: auto;
    }
    p {
        font-size: 2rem;
        color: #fff;
        line-height: 1.6;
        margin-bottom: 1.6rem;
        a {
            color: #5ac832;
            border-bottom: solid 0.1rem #5ac832;
        }
    }
    &__btn-knew {
        border: 0;
        width: 24.5rem;
        height: 5.4rem;
        background-color: #5ac832;
        color: #fff;
        text-align: center;
        line-height: 5.4rem;
        display: block;
        margin: 0 auto;
        font-size: 2.6rem;
    }
}