.privacy-detail {
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 300;
    pointer-events: none;
    opacity: 0;
    transition: all 500ms ease;
    &.is-active {
        pointer-events: auto;
        opacity: 1;
    }
    &__btn-close {
        width: 6.9rem;
        height: 7rem;
        position: absolute;
        top: 2rem;
        right: 4rem;
        background: transparent url('../images/privacy-detail-btn-close@2x.png') no-repeat 0 0;
        background-size: cover;
        border: 0;
    }
    &__overlay {
        justify-content: center;
        align-items: flex-start;
        display: flex;
        width: 100%;
        height: 100%;
        background-color: rgba(0,0,0,0.5);
        box-sizing: border-box;
        padding: 8rem 0;
        overflow-y: auto;
    }
    &__wrap {
        width: 76.8rem;
        background-color: rgba(255,255,255,0.8);
        padding: 9rem 5rem;
        box-sizing: border-box;
        position: relative;
    }
    &__section {
        &:not(:last-child) {
            margin-bottom: 4rem;
        }
        h3 {
            font-size: 3rem;
            color: #000;
            line-height: 1.2;
        }
        p {
            font-size: 3rem;
            color: #000;
            line-height: 1.2;
        }
    }
}