.event {
    background: #000 url('../images/event-bg.jpg') no-repeat center top;
    height: 216.3rem;
    position: relative;
    box-sizing: border-box;
    padding-top: 4rem;
    &__cht-right {
        width: 68.7rem;
        height: 84.5rem;
        position: absolute;
        left: 50%;
        bottom: 1.7rem;
        transform: translate3d(40%,0,0);
        z-index: 50;
        pointer-events: none;
        @include susy-breakpoint($s-320, $c8) {
            display: none;
        }
    }
    &__cht-left {
        width: 96.5rem;
        height: 166rem;
        position: absolute;
        left: 50%;
        transform: translate3d(-99%,0,0);
        bottom: 1.7rem;
        z-index: 30;
        pointer-events: none;
        @include susy-breakpoint($s-320, $c8) {
            display: none;
        }
    }
    &__title {
        text-align: center;
        img {
            width: auto;
            height: 10.1rem;
            display: inline-block;
        }
    }
    &__btn-more {
        display: block;
        position: absolute;
        width: 27.1rem;
        height: 10.9rem;
        background: transparent url('../images/event-btn-more@2x.png') no-repeat center top;
        background-size: cover;
        font-size: 0;
        left: 50%;
        transform: translate3d(-50%,0,0);
        bottom: 6rem;
        border: 0;
    }
    &__qrcode {
        width: 41.8rem * 0.8;
        height: 41.8rem * 0.8;
        background: transparent url('../images/qrcode-pc-btn@2x.png') no-repeat center top;
        background-size: cover;
        position: fixed;
        right: 0;
        top: 50%;
        transform: translate3d(23%,-60%,0);
        z-index: 350;
        opacity: 1;
        transition: all 500ms ease;
        &.is-active {
            opacity: 1;
            pointer-events: auto;
        }
        @include susy-breakpoint($s-320, $c8) {
            display: none;
            &.is-active {
                display: none;
            }
        }
    }
    &__btn-qrcode {
        display: block;
        width: 24.4rem;
        height: 24.4rem;
        background: transparent url('../images/qrcode-mobile@2x.png') no-repeat center top;
        background-size: cover;
        font-size: 0;
        right: -3rem;
        bottom: 50%;
        transform: translate3d(0,200%,0);
        display: none;
        position: fixed;
        opacity: 1;
        transition: all 500ms ease;
        &.is-active {
            opacity: 1;
            pointer-events: auto;
        }
        z-index: 400;
        @include susy-breakpoint($s-320, $c8) {
            display: block;
        }
    }
    // label {
    //     display: block;
    //     width: 24.4rem;
    //     height: 24.4rem;
    //     background: transparent url('../images/qrcode-mobile@2x.png') no-repeat center top;
    //     background-size: cover;
    //     font-size: 0;
    // }
}

.event-block-1 {
    background: transparent url('../images/event-block-1@2x.png') no-repeat center top;
    background-size: cover;
    width: 59.2rem;
    height: 54.1rem;
    margin-top: 3.4rem;
    position: relative;
    z-index: 20;
    &__item-1 {
        width: 21.1rem;
        height: 22rem;
        position: absolute;
        left: 6rem;
        top: 11.5rem;
    }
    &__item-2 {
        width: 21.1rem;
        height: 22rem;
        position: absolute;
        left: 32.5rem;
        top: 11.5rem;
    }
    &__label {
        height: 3.8rem;
        position: absolute;
        font-size: 3rem;
        color: #ffe3be;
        line-height: 3.8rem;
        right: -0.9rem;
        top: -1rem;
        box-shadow: 0 0.2rem 1rem #000;
        span {
            box-sizing: border-box;
            display: block;
            position: relative;
            z-index: 20;
            width: 100%;
            height: 100%;
            border: solid 0.1rem #ffe3be;
            padding: 0 1.6rem;
        }
        &::before {
            content: "";
            width: 3.2rem;
            height: 100%;
            background-color: #012e58;
            position: absolute;
            z-index: 5;
            right: -0.4rem;
            top: 0;
            transform: skewY(-15deg) translate3d(0,0.8rem,0);
            box-shadow: 0 0.2rem 1rem #000;
        }
        &::after {
            content: "";
            width: calc(100% + 0.8rem);
            height: calc(100% + 0.8rem);
            background-color: #013e77;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate3d(-50%,-50%,0);
            z-index: 10;
        }
    }
    &__item-name {
        box-sizing: border-box;
        width: 21rem;
        height: 8rem;
        font-size: 3rem;
        color: #ffe3be;
        position: absolute;
        left: 0;
        bottom: 0;
        line-height: 1.2;
        padding-top: 0.5rem;
        text-align: center;
    }
}

